.card-tags {
    font-size: 0.8rem !important;
    color: #00a6f3;
}

.card {
    margin: 4px 20px 20px 20px;
    padding: 0px;
    position: relative;
    transition: transform .15s ease-out;
    box-shadow: 3px 4px 10px 0px rgba(0, 0, 0, 0.2);
    border: 0px;
}

.card:hover {
    transform: scale(1.01);
}

.btn {
    font-family: 'Abel', sans-serif;
    border-radius: 2px;
    margin: 2px;
}

.gh-btn {
    position: absolute;
    left: 10px;
    bottom: 60px;
}

.sl-btn {
    position: absolute;
    left: 102px;
    bottom: 60px;

}

.card-text {
    margin-bottom:45px;
}

.card-body {

    font-family: 'Abel', sans-serif;
}

.card-tags {
    color: #919191;
}

/* .card-footer {
    background-color: #00CCFF;
} */

@media only screen and (max-width: 600px) {
    .card {
        width: 96% !important;


    }
}